* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: #7c7c7c;
  justify-content: center;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}

.wrap {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

h1 {
  color: #222;
  margin: 80px auto 20px;
}

.grid {
  grid-template-rows: repeat(9, auto);
  grid-template-columns: repeat(9, auto);
  gap: .5vmin;
  display: grid;
}

.cell {
  height: 8vmin;
  width: 8vmin;
  color: #222;
  cursor: pointer;
  background-color: #f2f2f2;
  border-radius: .5vmin;
  justify-content: center;
  align-items: center;
  font-size: 3vmin;
  display: flex;
}

.cell:nth-child(3), .cell:nth-child(6) {
  margin-right: .5vmin;
}

.cell:nth-child(19), .cell:nth-child(46) {
  margin-bottom: .5vmin;
}

.numbers {
  grid-template-columns: repeat(5, auto);
  gap: .5vmin;
  margin-top: 2vmin;
  display: grid;
}

.number, .remove {
  height: 8vmin;
  color: #222;
  cursor: pointer;
  background-color: #f2f2f2;
  border-radius: .5vmin;
  justify-content: center;
  align-items: center;
  font-size: 3vmin;
  display: flex;
}

